<template>
  <b-card
    class="blog-wrapper"
  >
    <!-- form -->
    <b-form class="mt-2">
      <b-row>
        <b-col md="6">
          <b-form-group
            label="Current Password"
            label-for="current_password"
            class="mb-2"
          >
          <validation-provider
              #default="{ errors }"
              name="Current Password"
              rules="required"
            >
            <b-form-input
                id="current_password"
                v-model="data.current_password"
                name="current_password"
                :state="errors.length > 0 ? false:null"
                type="password"
                placeholder="Type Current Password"
                autofocus
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group
            label="New Password"
            label-for="new_password"
            class="mb-2"
          >
          <validation-provider
              #default="{ errors }"
              name="New Password"
              rules="required"
            >
            <b-form-input
                id="new_password"
                v-model="data.new_password"
                name="new_password"
                :state="errors.length > 0 ? false:null"
                type="password"
                placeholder="Type New Password"
                autofocus
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col
          cols="12"
          class="mt-50"
        >
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-1"
            @click="sendData"
          >
            Save Changes
          </b-button>
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            variant="outline-secondary"
            :to="{ name: 'blog' }"
          >
            Cancel
          </b-button>
        </b-col>
      </b-row>
    </b-form>
    <!--/ form -->
  </b-card>
</template>

<script>
import { ValidationProvider } from 'vee-validate'
import {
  BCard, BMedia, BCardText, BMediaBody, BForm, BRow, BCol, BFormGroup, BFormInput, BButton
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { required } from '@validations'

export default {
  components: {
    BCard,
    BMedia,
    BCardText,
    BMediaBody,
    BForm,
    BRow,
    BCol,
    BButton,
    BFormGroup,
    BFormInput,
    ValidationProvider,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      data: {
        current_password: null,
        new_password: null,
      },
      tag: [],
    }
  },
  methods: {
    sendData() {
      const userData = JSON.parse(localStorage.getItem('userData'))
      const formData = new FormData()
      formData.append('user_id', userData.id)
      formData.append('current_password', this.data.current_password)
      formData.append('new_password', this.data.new_password)
      this.$swal({
        title: 'Are you sure?',
        text: 'You won\'t be able to revert this!',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, update it!',
        cancelButtonText: 'No, cancel!',
        confirmButtonClass: 'btn btn-primary',
        cancelButtonClass: 'btn btn-danger mr-1',
        buttonsStyling: false,
        reverseButtons: true,
      }).then(result => {
        if (result.value) {
          this.$http.post('change-password', formData).then(response => {
            if (response.data.status === true) {
              const toaster = 'b-toaster-top-center'
              this.$bvToast.toast(response.data.message, {
                title: 'Notification',
                toaster,
                variant: 'success',
                solid: false,
                appendToast: false,
              })
              setTimeout(() => {
                this.$router.push({ name: 'home' })
              }, 1000)
            } else {
              const toaster = 'b-toaster-top-center'
              this.$bvToast.toast(response.data.message, {
                title: 'Notification',
                toaster,
                variant: 'danger',
                solid: false,
                appendToast: false,
              })
            }
          })
        } else if (
          // Read more about handling dismissals
          // eslint-disable-next-line no-undef
          result.dismiss
        ) {
          this.$swal(
            'Cancelled',
            'Your profile is safe :)',
            'error',
          )
        }
      })
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
@import '@core/scss/vue/libs/quill.scss';
@import '@core/scss/vue/pages/page-blog.scss';
